import React from "react";
import styles from "../../css/Modal.module.css";
export const ModalContent = ({ job, type, checkDeadline }) => {
  return (
    <>
      {type === "guarantee" ? (
        <div className={styles.jobDetails}>
          <div className={styles.jobDetail}>
            <span style={{ flex: "1" }}></span>
            <span style={{ flex: "4" }}>
              {checkDeadline(job.apply_deadline)}
            </span>
          </div>
          <div className={styles.jobDetail}>
            <span style={{ flex: "1" }}></span>
            <span style={{ flex: "4" }}>
              {job.apply_work ? job.apply_work : ""}
            </span>
          </div>
          <div className={styles.jobDetail}>
            <span style={{ flex: "1" }}> </span>
            <span style={{ flex: "4" }}>
              {job.apply_detail ? job.apply_detail : ""}
            </span>
          </div>
          <div className={styles.jobDetail}>
            <span style={{ flex: "1" }}></span>
            <span style={{ flex: "4" }}>
              {job.company_name ? job.company_name : ""}
            </span>
          </div>
          <div className={styles.jobDetail}>
            <span style={{ flex: "1" }}></span>
            <span style={{ flex: "4" }}>
              {job.work_place ? job.work_place : ""}
            </span>
          </div>
          <div className={styles.jobDetail}>
            <span style={{ flex: "1" }}></span>
            <span style={{ flex: "4" }}>
              {job.work_day ? job.work_day : ""}
            </span>
          </div>
          <div className={styles.jobDetail}>
            <span style={{ flex: "1" }}></span>
            <span style={{ flex: "4" }}>
              {job.work_hour ? job.work_hour : ""}
            </span>
          </div>
          <div className={styles.jobDetail}>
            <span style={{ flex: "1" }}></span>{" "}
            <span style={{ flex: "4" }}>
              {job.work_term}/{job.work_term}
            </span>
          </div>
          <div className={styles.jobDetail}>
            <span style={{ flex: "1" }}></span>
            <span style={{ flex: "4" }}>
              {job.work_pay ? job.work_pay : ""}
            </span>
          </div>
          <div className={styles.jobDetail}>
            <span style={{ flex: "1" }}></span>
            <span style={{ flex: "4" }}>
              {job.apply_age ? job.apply_age : ""}
            </span>
          </div>
          <div className={styles.jobDetail}>
            <span style={{ flex: "1" }}></span>
            <span style={{ flex: "4" }}>
              {job.work_experience ? job.work_experience : ""}
            </span>
          </div>
          <div className={styles.jobDetail}>
            <span style={{ flex: "1" }}></span>
            <span style={{ flex: "4" }}>
              {job.apply_deadline ? job.apply_deadline : ""}
            </span>
          </div>
        </div>
      ) : (
        <div className={styles.jobDetails}>
          <div className={styles.jobDetail}>
            <span style={{ flex: "1" }}></span>
            <span style={{ flex: "4" }}>
              {job.wantedTitle ? job.wantedTitle : ""}
            </span>
          </div>
          <div className={styles.jobDetail}>
            <span style={{ flex: "1" }}></span>
            <span style={{ flex: "4" }}>
              {job.detCnts ? job.detCnts : ""}
            </span>
          </div>
          <div className={styles.jobDetail}>
            <span style={{ flex: "1" }}></span>
            <span style={{ flex: "4" }}>
              {job.clltPrnnum ? job.clltPrnnum : ""}
            </span>
          </div>
          <div className={styles.jobDetail}>
            <span style={{ flex: "1" }}></span>
            <span style={{ flex: "4" }}>
              {job.plbizNm ? job.plbizNm : ""}
            </span>
          </div>
          <div className={styles.jobDetail}>
            <span style={{ flex: "1" }}></span>
            <span style={{ flex: "4" }}>
              {job.plDetAddr ? job.plDetAddr : ""}
            </span>
          </div>
          <div className={styles.jobDetail}>
            <span style={{ flex: "1" }}></span>
            <span style={{ flex: "4" }}>{job.clerk ? job.clerk : ""}</span>
          </div>
          <div className={styles.jobDetail}>
            <span style={{ flex: "1" }}></span>
            <span style={{ flex: "4" }}>
              {job.clerkContt ? job.clerkContt : ""}
            </span>
          </div>
          <div className={styles.jobDetail}>
            <span style={{ flex: "1" }}></span>{" "}
            <span style={{ flex: "4" }}>
              {job.age}/{job.ageLim}
            </span>
          </div>
          <div className={styles.jobDetail}>
            <span style={{ flex: "1" }}></span>
            <span style={{ flex: "4" }}>
              {job.ftAcptDd ? job.ftAcptDd : ""}
            </span>
          </div>
          <div className={styles.jobDetail}>
            <span style={{ flex: "1" }}></span>
            <span style={{ flex: "4" }}>
              {job.toAcptDd ? job.toAcptDd : ""}
            </span>
          </div>
        </div>
      )}
    </>
  );
};
