import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import Spinner from "./Spinner";
import { useRecoilState } from "recoil";
import userState from "../../recoil/userState";
//     
//   
const RedirectHandler = () => {
  const [code, setCode] = useState(null);
  const [prevCode, setPrevCode] = useState(null);
  const [loggedInUser, setLoggedInUser] = useRecoilState(userState);
  const navigate = useNavigate();
  useEffect(() => {
    if (code && code !== prevCode) {
      const url = "http://123.212.65.172:8000/accounts/kakao/callback/";
      console.log(" URL:", `${url}?code=${code}`);
      axios
        .get(url, {
          params: { code },
        })
        .then((response) => {
          console.log(" :", response);
          setLoggedInUser({
            isLoggedIn: true,
            userName: response.data.name,
            email: response.data.email,
          });
          navigate("/loginSuccess");
        })
        .catch((error) => {
          console.error(" :", error);
          navigate("/error");
        });
      setPrevCode(code);
    }
  }, [code, navigate, prevCode]);
  useEffect(() => {
    const newCode = new URL(window.location.href).searchParams.get("code");
    if (newCode) {
      setCode(newCode);
    }
  }, []);
  return <Spinner />;
};
export default RedirectHandler;
