import React, { useState } from "react";
import styles from "../css/companyPartnerApply.module.css";
import { Link } from "react-router-dom";
import { Axios } from "../api/axios";

const CompanyPartnerApply = (props) => {
  const [formData, setFormData] = useState({
    company_name: "",
    company_address: "",
    company_number: "",
    company_call: "",
    company_boss: "",
    company_size: "",
    company_homepage: "",
    company_logo: null,
    work_place: "",
    work_day: "",
    work_hour: "",
    work_pay: "",
    work_term: "",
    work_experience: "",
    apply_num: "",
    apply_deadline: "",
    apply_sex: "",
    apply_age: "",
    apply_work: "",
    apply_detail: "",
  });
  const handleFormSubmit = async (e) => {
    e.preventDefault();

    const url = "/company/register/"; // API endpoint URL

    try {
      console.log(formData);
      const response = await Axios.post(url, formData, {
        headers: {
          "Content-Type": "application/json",
        },
      });

      // Successfully submitted data to the server
      alert("  .");
      // Additional handling or redirection if needed
    } catch (error) {
      console.error("  :", error);
      // Data submission error handling
      alert("  .  .");
    }
  };

  const handleValueChange = (e) => {
    const { name, value } = e.target;
    setFormData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
  };
  const handleFileChange = (e) => {
    const { name, files } = e.target;
    setFormData((prevData) => ({
      ...prevData,
      [name]: files[0],
    }));
  };

  return (
    <form onSubmit={handleFormSubmit}>
      <div className={styles.cp_container}>
        <div className={styles.cp_title}>
          <h1>
            <b>  </b>
          </h1>
        </div>
        {/*  */}
        <div className={styles.cp_inforContainer}>
          <div className={styles.cp_infortitle}>
            <p> </p>
          </div>
          <div className={styles.cp_inforWarning}>
            <p>*  </p>
          </div>
          <div className={styles.cp_inforcontentBox}>
            <div className={styles.cp_inforcontents}>
              <div className={styles.cp_inforcontent}>
                <div className={styles.cp_inforcontenttext}>
                  <span>*</span>
                  <p>/</p>
                </div>
                <div className={styles.cp_inforcontenttext}>
                  <span>*</span>
                  <p></p>
                </div>
                <div className={styles.cp_inforcontenttext}>
                  <span>*</span>
                  <p>/ </p>
                </div>
                <div className={styles.cp_inforcontenttext}>
                  <span>*</span>
                  <p>/ </p>
                </div>
              </div>
              <div className={styles.cp_inforcontent}>
                <div className={styles.cp_inforcontentinput}>
                  <input
                    type="text"
                    name="company_name"
                    value={formData.company_name}
                    onChange={handleValueChange}
                    placeholder=""
                  />
                </div>
                <div className={styles.cp_inforcontentinput}>
                  <input
                    type="number"
                    name="company_number"
                    value={formData.company_number}
                    onChange={handleValueChange}
                    placeholder="135-82-11191"
                  />
                </div>
                <div className={styles.cp_inforcontentinput}>
                  <input
                    type="text"
                    name="company_address"
                    value={formData.company_address}
                    onChange={handleValueChange}
                    placeholder="     81 "
                  />
                </div>
                <div className={styles.cp_inforcontentinput}>
                  <input
                    type="text"
                    name="company_call"
                    value={formData.company_call}
                    onChange={handleValueChange}
                    placeholder="031-330-4114"
                  />
                </div>
              </div>
            </div>

            <div className={styles.cp_inforcontents}>
              <div className={styles.cp_inforcontent}>
                <div className={styles.cp_inforcontenttext}>
                  <span>*</span>
                  <p></p>
                </div>
                <div className={styles.cp_inforcontenttext}>
                  <p></p>
                </div>
                <div className={styles.cp_inforcontenttext}>
                  <p></p>
                </div>
                <div className={styles.cp_inforcontenttext}>
                  <p></p>
                </div>
              </div>
              <div className={styles.cp_inforcontent}>
                <div className={styles.cp_inforcontentinput}>
                  <input
                    type="text"
                    name="company_boss"
                    value={formData.company_boss}
                    onChange={handleValueChange}
                    placeholder=""
                  />
                </div>
                <div className={styles.cp_inforcontentinput}>
                  <input
                    type="number"
                    name="company_size"
                    value={formData.company_size}
                    onChange={handleValueChange}
                    placeholder="6()"
                  />
                </div>
                <div className={styles.cp_inforcontentinput}>
                  <input
                    type="text"
                    name="company_homepage"
                    value={formData.company_homepage}
                    onChange={handleValueChange}
                    placeholder="https://www.hufs.ac.kr"
                  />
                </div>
                <div className={styles.cp_inforcontentinput}>
                  <input
                    type="file"
                    name="company_logo"
                    onChange={handleFileChange}
                    placeholder=""
                  />
                </div>
              </div>
            </div>
          </div>
        </div>

        {/*   */}
        <div className={styles.cp_inforContainer}>
          <div className={styles.cp_infortitle}>
            <p> </p>
          </div>
          <div className={styles.cp_inforWarning}>
            <p>*  </p>
          </div>
          <div className={styles.cp_inforcontentBox}>
            <div className={styles.cp_inforcontents}>
              <div className={styles.cp_inforcontent}>
                <div className={styles.cp_inforcontenttext}>
                  <span>*</span>
                  <p></p>
                </div>
                <div className={styles.cp_inforcontenttext}>
                  <span>*</span>
                  <p></p>
                </div>
                <div className={styles.cp_inforcontenttext}>
                  <span>*</span>
                  <p></p>
                </div>
                <div className={styles.cp_inforcontenttext}>
                  <span>*</span>
                  <p></p>
                </div>
              </div>
              <div className={styles.cp_inforcontent}>
                <div className={styles.cp_inforcontentinput}>
                  <input
                    type="text"
                    name="work_place"
                    value={formData.work_place}
                    onChange={handleValueChange}
                    placeholder="     81 "
                  />
                </div>
                <div className={styles.cp_inforcontentinput}>
                  <input
                    type="text"
                    name="work_day"
                    value={formData.work_day}
                    onChange={handleValueChange}
                    placeholder=", , "
                  />
                </div>
                <div className={styles.cp_inforcontentinput}>
                  <input
                    type="text"
                    name="work_hour"
                    value={formData.work_hour}
                    onChange={handleValueChange}
                    placeholder=" 9 ~  6 "
                  />
                </div>
                <div className={styles.cp_inforcontentinput}>
                  <input
                    type="text"
                    name="work_pay"
                    value={formData.work_pay}
                    onChange={handleValueChange}
                    placeholder="250"
                  />
                </div>
              </div>
            </div>

            <div className={styles.cp_inforcontents}>
              <div className={styles.cp_inforcontent}>
                <div className={styles.cp_inforcontenttext}>
                  <span>*</span>
                  <p></p>
                </div>
                <div className={styles.cp_inforcontenttext}>
                  <p></p>
                </div>
              </div>
              <div className={styles.cp_inforcontent}>
                <div className={styles.cp_inforcontentinput}>
                  <div className={styles.cp_radio}>
                    <input
                      type="radio"
                      id="1Month"
                      name="work_term"
                      value="1"
                      onChange={handleValueChange}
                    />
                    <label for="1Month">1</label>
                    <input
                      type="radio"
                      id="3Month"
                      name="work_term"
                      value="3"
                      onChange={handleValueChange}
                    />
                    <label for="3Month">3</label>
                    <input
                      type="radio"
                      id="6Month"
                      name="work_term"
                      value="6"
                      onChange={handleValueChange}
                    />
                    <label for="6Month">6</label>
                    <input
                      type="radio"
                      id="12Month"
                      name="work_term"
                      value="12"
                      onChange={handleValueChange}
                    />
                    <label for="12Month">1 </label>
                  </div>
                </div>
                <div className={styles.cp_inforcontentinput}>
                  <input
                    type="text"
                    name="work_experience"
                    value={formData.work_experience}
                    onChange={handleValueChange}
                    placeholder=" 1"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>

        {/*  */}
        <div className={styles.cp_inforContainer}>
          <div className={styles.cp_infortitle}>
            <p> </p>
          </div>
          <div className={styles.cp_inforWarning}>
            <p>*  </p>
          </div>
          <div className={styles.cp_inforcontentBox}>
            <div className={styles.cp_inforcontents}>
              <div className={styles.cp_inforcontent}>
                <div className={styles.cp_inforcontenttext}>
                  <span>*</span>
                  <p> </p>
                </div>
                <div className={styles.cp_inforcontenttext}>
                  <span>*</span>
                  <p> </p>
                </div>
                <div className={styles.cp_inforcontenttext}>
                  <span>*</span>
                  <p></p>
                </div>
                <div className={styles.cp_inforcontenttext}>
                  <span>*</span>
                  <p></p>
                </div>
              </div>
              <div className={styles.cp_inforcontent}>
                <div className={styles.cp_inforcontentinput}>
                  <input
                    type="number"
                    name="apply_num"
                    value={formData.apply_num}
                    onChange={handleValueChange}
                    placeholder="2 "
                  />
                </div>
                <div className={styles.cp_inforcontentinput}>
                  <input
                    type="text"
                    name="apply_deadline"
                    value={formData.apply_deadline}
                    onChange={handleValueChange}
                    placeholder="2023 8 30"
                  />
                </div>
                <div className={styles.cp_inforcontentinput}>
                  <div className={styles.cp_radio}>
                    <input
                      type="radio"
                      id="ignore"
                      name="apply_sex"
                      value="X"
                      onChange={handleValueChange}
                    />
                    <label for="1Month"></label>
                    <input
                      type="radio"
                      id="Men"
                      name="apply_sex"
                      value="M"
                      onChange={handleValueChange}
                    />
                    <label for="3Month"></label>
                    <input
                      type="radio"
                      id="Woman"
                      name="apply_sex"
                      value="W"
                      onChange={handleValueChange}
                    />
                    <label for="6Month"></label>
                  </div>
                </div>
                <div className={styles.cp_inforcontentinput}>
                  <input
                    type="number"
                    name="apply_age"
                    value={formData.apply_age}
                    onChange={handleValueChange}
                    placeholder="80 "
                  />
                </div>
              </div>
            </div>

            <div className={styles.cp_inforcontents}>
              <div className={styles.cp_inforcontent}>
                <div className={styles.cp_inforcontenttext}>
                  <span>*</span>
                  <p></p>
                </div>
                <div className={styles.cp_inforcontenttext}>
                  <span>*</span>
                  <p></p>
                </div>
              </div>
              <div className={styles.cp_inforcontent}>
                <div className={styles.cp_inforcontentinput}>
                  <input
                    type="text"
                    name="apply_work"
                    value={formData.apply_work}
                    onChange={handleValueChange}
                    placeholder=""
                  />
                </div>
                <div className={styles.cp_inforcontentinput}>
                  <input
                    type="text"
                    name="apply_detail"
                    value={formData.apply_detail}
                    onChange={handleValueChange}
                    placeholder=", "
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div className={styles.cp_inforPower}>
        <h2>
          <input type="checkbox" />
          <label> </label>
        </h2>
      </div>
      <div className={styles.cp_inforPowerText}>
        <p>
                 
              .
          <br />   ,      
          .
          <br />       .
        </p>
        <button
          className={styles.cp_btn}
          type="submit"
          onClick={handleFormSubmit}
        >
           
        </button>
      </div>
    </form>
  );
};

export default CompanyPartnerApply;
