import React, { useState } from "react";
import styles from "../../css/Apply.module.css";
import { Link } from "react-router-dom";
import { Axios } from "../../api/axios";

const AInput = ({ convertScreen }) => {
  const [name, setName] = useState("");
  const [phone_number, setPhoneNumber] = useState("");

  const handleSubmit = async (e) => {
    e.preventDefault();

    const formData = {
      name: name,
      phone_number: phone_number,
    };
    try {
      const request = await Axios.get("/guarantee/company/", {
        params: formData,
      });
      console.log(request);
      convertScreen(request.data);
    } catch (error) {
      console.error(error);
      alert("   .  .");
    }
  };

  return (
    <form onSubmit={handleSubmit}>
      <div
        style={{
          textAlign: "center",
          paddingTop: "30px",
        }}
      >
        <h4 style={{ textAlign: "center", paddingBottom: "10px" }}>
           
        </h4>
        <div style={{ textAlign: "center" }}>
          <input
            type="tel"
            placeholder={"010-1234-1234"}
            pattern="[0-9]{2,3}-[0-9]{3,4}-[0-9]{4}"
            className={styles.box}
            value={phone_number}
            onChange={(e) => setPhoneNumber(e.target.value)}
          ></input>
        </div>
      </div>
      <div
        style={{
          textAlign: "center",
          paddingTop: "30px",
        }}
      >
        <h4 style={{ textAlign: "center", paddingBottom: "10px" }}>
           
        </h4>
        <div style={{ textAlign: "center" }}>
          <input
            type="text"
            placeholder={" "}
            className={styles.box}
            value={name}
            onChange={(e) => setName(e.target.value)}
          ></input>
        </div>
      </div>
      <div
        style={{
          textAlign: "center",
          paddingTop: "70px",
        }}
      >
        <button type="submit" className={styles.hoverButton}>
          
        </button>
      </div>
      {/*  Route   Link    . */}
    </form>
  );
};

export default AInput;
